@import url('../../../css/colors.css');

.errorModal {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.7);
    text-align: center;
    z-index: 1000;
}

.errorModal h2 {
    margin-top: 200px;
    font-size: 40px;
    color: tomato;
}

.errorModal p {
    margin: 15px 0;
    font-size: 20px;
    color: var(--color-blue);
}
